@import url(https://fonts.googleapis.com/css?family=Muli:400,300italic,300,400italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,
html {
	margin: 0;
	font-family: muli;
	height: 100%;
	padding: 0;
	overflow-x: hidden;
}

h1,
h2,
h3 {
	margin: 0;
}

.container {
	display: flex;
	height: 100vh;
	justify-content: center;
	align-items: center;
	background: #999;
}

.window {
	background: #fff;
	width: 470px;
	display: flex;
	min-height: 400px;
	position: relative;
	box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.2);
}
.options {
	display: flex;
	margin-top: 25px;
	justify-content: space-between;
}
.main-content {
	padding: 50px 46px 25px 20px;
	box-sizing: border-box;
	display: flex;
	color: #222;
	width: 100%;
	height: 100%;
	flex-flow: column;
}

h1 {
	letter-spacing: 0px;
	letter-spacing: 0.02rem;
	font-size: 48px;
	font-size: 3rem;
	text-transform: uppercase;
}

h3 {
	color: #666;
	font-size: 19px;
	font-size: 1.2rem;
	text-transform: uppercase;
}

.description {
	margin-top: 20px;
	width: 100%;
}

.highlight-window {
	height: 550px;
	width: 400px;
	background: #ccc;
	background: url(/ra-components-func/static/media/item-blue.2103f69b.jpg);
	background-size: cover;
	box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.2);
	z-index: 10;
	position: relative;
	background-position: center top;
}

.color {
	height: 30px;
	cursor: pointer;
	width: 30px;
	background: #eee;
	border: 1px solid #eee;
	position: relative;
}
.highlight-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	filter: alpha(opacity=0);
	background: url(/ra-components-func/static/media/item-black.dd74251d.jpg);
	background-size: cover;
	transition: opacity 0.4s ease;
	background-position: center top;
}
.background-element {
	background: #457;
	position: absolute;
	width: 120%;
	height: 400px;
	left: -50px;
	top: -80px;
	transform: rotate(-5deg);
	transition: background 0.6s ease;
}
.color.overlay {
	position: absolute;
	z-index: 10;
	background: transparent;
	top: -1px;
	left: -1px;
	transform: translateX(45px);
	border: 2px solid #fff;
	outline: 2px solid #ccc;
	transition: transform 0.3s ease;
}
.color-a {
	background: #333;
	margin-right: 14px;
}

.color-b {
	background: #457;
}

.color-picker {
	display: flex;
	width: 77px;
	margin-top: 5px;
	position: relative;
}
a {
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
	color: #666;
}
button {
	background: #333;
	border: none;
	font-weight: 400;
	height: 40px;
	margin-top: auto;
	margin-bottom: auto;
	padding-left: 25px;
	padding-right: 25px;
	box-sizing: border-box;
	color: #fff;
	cursor: pointer;
	transition: all 0.3s ease;
	text-transform: uppercase;
}
button:hover {
	background: #555;
	transition: all 0.3s ease;
}
.divider {
	width: 80%;
	height: 1px;
	background: #ddd;
	margin-top: 20px;
	margin-bottom: 20px;
	margin-left: auto;
	margin-right: auto;
}
.color-options {
	display: flex;
	width: 50%;
	flex-flow: column;
}
.size-picker {
	display: flex;
	flex-flow: column;
}
.small {
	font-size: 11px;
	font-size: 0.7rem;
	color: #999;
	margin-top: 10px;
}
.align-right {
	align-self: flex-end;
}
.size-desc {
	align-self: flex-end;
}

.purchase-info {
	justify-content: space-between;
	display: flex;
}
.price {
	font-size: 40px;
	font-size: 2.5rem;
}

.selection {
	background: #fff;
}

.range-picker {
	font-size: 16px;
	font-size: 1rem;
	display: flex;
	margin-top: 5px;
	align-items: center;
	line-height: 0.9em;
}
.range-picker div {
	display: flex;
	border-right: 1px solid #bbb;
	border-top: 1px solid #bbb;
	border-bottom: 1px solid #bbb;
	align-items: center;
	color: #bbb;
	width: 30px;
	box-sizing: border-box;
	cursor: pointer;
	justify-content: center;
	height: 30px;
	transition: background 0.5s ease;
}
.highlight-window.mobile {
	display: none;
}
.range-picker .active:hover {
	background: #fff;
}
.range-picker .active {
	transform: scale(1.2);
	background: #fff;
	margin-right: 3px;
	margin-left: 2px;
	color: #333;
	border: 1px solid #666;
	z-index: 1;
}
.check {
	position: absolute;
	right: 0px;
	left: 0px;
	margin-left: auto;
	margin-right: auto;
	background: transparent;
	width: 0px;
	bottom: -3px;
	border-left: 10px solid transparent;
	border-bottom: 10px solid #ccc;
	border-right: 10px solid transparent;
	height: 0px;
}
.range-picker div:hover {
	background: #eee;
	transition: background 0.2s;
}
.range-picker div:first-child {
	border-left: 1px solid #bbb;
}
.range-picker div.active:first-child {
	border-left: 1px solid #333;
}

@media (max-width: 850px) {
	body {
		background: #fff;
		overflow: auto;
	}
	h1 {
		font-size: 2rem;
	}
	h3,
	h2 {
		font-size: 1rem;
	}
	.container {
		background: #fff;
		overflow: auto;
	}
	.background-element {
		display: none;
	}
	.main-content {
		overflow: auto;
		height: auto;
		padding-right: 25px;
	}
	.options {
		flex-flow: column;
	}
	.highlight-window {
		position: fixed;
		left: 0;
		height: 100%;
		width: 50%;
	}
	.window {
		overflow: auto;
		width: 50%;
		height: auto;
		position: absolute;
		box-shadow: none;
		right: 0;
	}
	.price {
		margin-bottom: 15px;
	}
	.button {
		width: 100%;
	}
	.color-options {
		margin-bottom: 15px;
	}
	.align-right {
		align-self: flex-start;
	}
	.purchase-info {
		flex-flow: column;
	}
}

@media (max-width: 550px) {
	.highlight-window {
		display: none;
	}
	body {
		overflow: auto;
		height: auto;
	}
	.highlight-window.mobile {
		position: relative;
		display: block;
		height: 500px;
		box-shadow: none;
		border: 1px solid #ccc;
		margin-top: 15px;
		width: 100%;
	}
	.container {
		overflow: auto;
	}
	.window {
		width: 100%;
		position: relative;
		height: 100%;
		overflow: auto;
	}
}

